<style>
</style>

<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>
    </Row>
    <Divider />
    <router-view></router-view>
    <Form ref="queryParamFrom"
      class="query-pram-from"
      :model="paramData.data">

      <Row>

        <i-col span="4"
          offset="1"
          class="page-col">
          <FormItem prop="appName">
            <i-input type="text"
              placeholder=""
              v-model="paramData.data.appName">
            </i-input>
          </FormItem>
        </i-col>

        <i-col span="2"
          offset="1"
          class="page-col">
          <FormItem prop="appCategory">
            <Select v-model="paramData.data.appCategory"
              @on-change="getdata"
              placeholder=""
              style="width:200px"
              clearable>
              <Option v-for="item in appCategoryEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
        </i-col>

        <i-col span="2"
          offset="1"
          class="page-col">
          <FormItem prop="authType">
            <Select v-model="paramData.data.authType"
              @on-change="getdata"
              placeholder=""
              style="width:200px"
              clearable>
              <Option v-for="item in authTypeEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
        </i-col>

        <i-col span="2"
          offset="1"
          class="page-col">
          <FormItem prop="appStatus">
            <Select v-model="paramData.data.appStatus"
              @on-change="getdata"
              placeholder=""
              style="width:200px"
              clearable>
              <Option v-for="item in appStatusEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
        </i-col>

        <i-col span="4"
          offset="18"
          class="button-group">
          <Button type="info"
            @click="pushAdd()"></Button>
          <Button type="success"
            @click="getdata()"></Button>
          <Button type="warning"
            @click="handleReset()"></Button>
          <Button type="primary"
            @click="syncAppApiRelationship()"></Button>
        </i-col>
      </Row>
    </Form>

    <br />
    <Table border
      :columns="columns"
      :data="tableData"
      class="list-button-group">
      <template slot-scope="{ row, index }"
        slot="action">
        <Button type="warning"
          size="small"
          style="margin-right: 5px"
          @click="showApiBindModal(row)"></Button>
        <Button type="primary"
          size="small"
          style="margin-right: 5px"
          @click="pushEditor(row.id)"></Button>
        <Button type="error"
          size="small"
          :disabled="globalButtonLoding"
          @click="deleteData(row.id ,index)"></Button>
      </template>
    </Table>
    <Row>
      <i-col span="6"
        offset="17"
        class="page-col">
        <Page :total="paramData.data.totalCount"
          :current="paramData.data.currentPage"
          :page-size="paramData.data.pageSize"
          @on-change="handlePage"
          @on-page-size-change='handlePageSize'
          show-sizer
          class="pagination" />
      </i-col>
    </Row>
    <AppApiModal :show-modal="showModal"
      :modal-title="modalTitle"
      :app-id="selectAppId"
      @closeApiBindModal="closeApiBindModal" />
    <Spin size="large"
      fix
      v-if="globalScreenLoding" />
  </div>
</template>
<script>
import AppApiModal from './AppApiModal.vue'
export default {
  components: {
    AppApiModal
  },
  data() {
    return {
      routerPath: this.$route.path,
      authTypeEunmArr: [
        {
          value: '0',
          label: ' ()'
        },
        {
          value: '1',
          label: '  ( )'
        },
        {
          value: '2',
          label: 'IP+ ()'
        },
        {
          value: '3',
          label: ' ( )'
        }
      ],
      appCategoryEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      appStatusEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      paramData: {
        data: {
          appStatus: '',
          authType: '',
          appCategory: '',
          appName: ''
        },
        page: {
          currentPage: 1,
          pageSize: 10
        },
        sort: {
          orderBy: 'id',
          sort: 'desc'
        }
      },
      columns: [
        { title: '', key: 'appName' },
        { title: '', key: 'appCategory' },
        { title: '', key: 'authType' },
        { title: '', key: 'appStatus' },
        { title: '', key: 'createTime' },
        { title: '', key: 'updateTime' },
        { title: '', key: 'operatorName' },
        { title: '', slot: 'action', width: 250, align: 'center' }
      ],
      tableData: [],
      showModal: false,
      selectAppId: '',
      modalTitle: ''
    }
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  mounted: function() {
    this.getdata()
  },
  methods: {
    handlePage(value) {
      this.paramData.page.currentPage = value
      this.getdata()
    },
    handlePageSize(value) {
      this.paramData.page.pageSize = value
      this.getdata()
    },
    rowClassName(row, index) {
      return this.utils.initTableListRowClass(index)
    },
    getdata() {
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.appInfoFind,
        this.paramData,
        response => {
          this.tableData = response.data.datas
          this.paramData.data.totalCount = response.data.page.totalCount
          this.paramData.data.pageSize = response.data.page.pageSize
          this.paramData.data.currentPage = response.data.page.currentPage
        }
      )
    },
    deleteData(id, index) {
      this.$store.commit('statusGlobalButtonLoding')
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.utils.netUtil.post(
            this.$store,
            this.API_PTAH.appInfoDelete,
            { id: id },
            () => {
              this.tableData.splice(index, 1)
              this.$Message.success('!')
              this.$store.commit('statusGlobalButtonLoding')
            },
            () => {
              this.$store.commit('statusGlobalButtonLoding')
            }
          )
        },
        onCancel: () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      })
    },
    syncAppApiRelationship() {
      this.$store.commit('statusGlobalScreenLoding')
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.syncAppApiRelationship,
        {},
        () => {
          this.$Message.success('!')
          this.$store.commit('statusGlobalScreenLoding')
        },
        () => {
          this.$store.commit('statusGlobalScreenLoding')
        }
      )
    },
    handleReset() {
      this.$refs.queryParamFrom.resetFields()
      this.getdata()
    },
    pushAdd() {
      this.$router.push(`${this.$route.path}/app-info-add`)
    },
    pushEditor(id) {
      this.$router.push({ path: `${this.$route.path}/app-info-editor/${id}` })
    },
    showApiBindModal(item) {
      this.modalTitle = `${item.appName} `
      this.showModal = true
      this.selectAppId = item.id
    },
    closeApiBindModal() {
      this.showModal = false
    }
  }
}
</script>