<style>
</style>


<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>

    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="apiInfoFrom"
          :model="apiInfoFrom"
          :rules="apiInfoFromValidate"
          :label-width="80">

          <FormItem label=""
            prop="apiName">
            <i-input v-model="apiInfoFrom.apiName"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="apiUri">
            <i-input v-model="apiInfoFrom.apiUri"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="proxyPath">
            <i-input v-model="apiInfoFrom.proxyPath"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="apiStatus">
            <Select v-model="apiInfoFrom.apiStatus"
              placeholder="">
              <Option v-for="item in apiStatusEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem>
            <Button type="primary"
              @click="handleSubmit('apiInfoFrom')"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
  </div>

</template>


<script>


export default {
  props: {
    query: { userId: 0 }
  },
  data() {
    return {
      routerPath: this.$route.path,
      apiStatusEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      apiInfoFrom: {
        id: this.$route.params.id,
        serverId: this.$route.params.sid,
        apiName: '',
        apiUri: '',
        proxyPath: '',
        apiStatus: ''
      },
      apiInfoFromValidate: {
        apiName: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        apiUri: [
          {
            pattern: /^\/\S*[A-Za-z0-9]+$/,
            required: true,
            message: ' `/` ',
            trigger: 'blur'
          }
        ],
        proxyPath: [
          {
            pattern: /^\/\S*[A-Za-z0-9]+$/,
            required: true,
            message: ' `/` ',
            trigger: 'blur'
          }
        ],
        apiStatus: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  mounted: function() {
    this.loadData()
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    loadData() {
      this.utils.netUtil.post(this.$store,this.API_PTAH.apiInfoFindById, this.apiInfoFrom, response => {
        this.apiInfoFrom = response.data.datas
      })
    },
    commitData() {
      this.apiInfoFrom.id = this.$route.params.id
      this.utils.netUtil.post(this.$store,this.API_PTAH.apiInfoUpdate, this.apiInfoFrom, () => {
        this.$Message.success('!')
        this.$router.push(
          `/server-info-list/api-info-list/${this.$route.params.sid}`
        )
      })
    }
  }
}
</script>
