<style>
</style>


<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>

    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="appInfoFrom"
          :model="appInfoFrom"
          :rules="fromValidate"
          :label-width="100">
          <FormItem label=""
            prop='appName'>
            <i-input v-model="appInfoFrom.appName"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="appCategory">
            <Select v-model="appInfoFrom.appCategory"
              placeholder="   ">
              <Option v-for="item in appCategoryEunmArr"
                :value="item.value"
                :disabled="item.disabled"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label=""
            prop='appStatus'>
            <Select v-model="appInfoFrom.appStatus"
              placeholder="">
              <Option v-for="item in appStatusEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label="API"
            prop='authType'
            v-if="notShowOnBasicApp()">
            <Select v-model="appInfoFrom.authType"
              placeholder="API"
              disabled>
              <Option v-for="item in authTypeEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
            <ipWhiteList v-show="showOnInnerApi()" />
            <unLoginAccessWhiteList :app-id="appInfoFrom.id"
              v-show="showOnPrivateTokenAuthType()" />
          </FormItem>

          <FormItem label=""
            prop="loginType"
            v-if="showOnPrivateTokenAuthType() && notShowOnBasicApp()">
            <Select v-model="appInfoFrom.loginType"
              placeholder="">
              <Option v-for="item in loginTypeEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label="SSO"
            prop="oauthBaseUrl"
            v-if="notShowOnBasicApp() && showOnSso()">
            <i-input v-model="appInfoFrom.oauthBaseUrl"
              placeholder="SSO"></i-input>
          </FormItem>

          <FormItem label="ID"
            prop='openId'
            v-if="notShowOnBasicApp()">
            <i-input v-model="appInfoFrom.openId"
              disabled
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop='secret'
            v-show="showOnOpenApi()">
            <i-input v-model="appInfoFrom.secret"
              disabled
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop='innerApiToken'
            v-show="showOnInnerApi()">
            <i-input v-model="appInfoFrom.innerApiToken"
              disabled
              placeholder=""></i-input>
          </FormItem>

          <FormItem label="PC"
            prop="pcPageUrl"
            v-if="showOnBasicApp()">
            <i-input v-model="appInfoFrom.pcPageUrl"
              placeholder="PC"></i-input>
          </FormItem>

          <FormItem label="PCicon"
            prop="pcMenuIcon"
            v-if="showOnBasicApp()">
            <i-input v-model="appInfoFrom.pcMenuIcon"
              placeholder="PCicon"></i-input>
          </FormItem>

          <FormItem label="PC"
            prop="menuSort"
            v-if="showOnBasicApp()">
            <i-input v-model="appInfoFrom.menuSort"
              placeholder="PCicon"></i-input>
          </FormItem>

          <FormItem label=""
            prop='createTime'>
            <i-input v-model="appInfoFrom.createTime"
              disabled
              placeholder=""></i-input>
          </FormItem>

          <FormItem label="	"
            prop='updateTime'>
            <i-input v-model="appInfoFrom.updateTime"
              disabled
              placeholder=""></i-input>
          </FormItem>

          <FormItem label="	"
            prop='operatorName'>
            <i-input v-model="appInfoFrom.operatorName"
              disabled
              placeholder=""></i-input>
          </FormItem>

          <FormItem>
            <Button type="primary"
              :disabled="globalButtonLoding"
              @click="handleSubmit('appInfoFrom')"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
  </div>

</template>


<script>
import ipWhiteList from '../ipWhiteList/ipWhiteList'
import unLoginAccessWhiteList from '../unLoginAccessWhiteList/unLoginAccessWhiteList'

export default {
  components: {
    ipWhiteList,
    unLoginAccessWhiteList
  },
  props: {
    query: { userId: 0 }
  },
  data() {
    return {
      routerPath: this.$route.path,
      loginTypeEunmArr: [
        {
          value: '0',
          label: 'API (//)'
        },
        {
          value: '1',
          label: 'SSO ()'
        },
        {
          value: '2',
          label: 'SSO ()'
        }
      ],
      appCategoryEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      authTypeEunmArr: [
        {
          value: '0',
          label: ' ()'
        },
        {
          value: '1',
          label: '  ( )'
        },
        {
          value: '2',
          label: 'IP+ ()'
        },
        {
          value: '3',
          label: ' ( )'
        }
      ],
      appStatusEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      appInfoFrom: {
        id: this.$route.params.id,
        createTime: '',
        updateTime: '',
        operatorName: '',
        appName: '',
        authType: '',
        appStatus: '',
        appCategory: '',
        pcPageUrl: '',
        pcMenuIcon: '',
        oauthBaseUrl: '',
        menuSort: 0,
        loginType: ''
      },
      fromValidate: {
        loginType: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        oauthBaseUrl: [
          {
            required: true,
            message: 'url',
            pattern: /^(http|ftp|https){1}[:]{1}[/]{2}([0-9a-zA-Z]|[-]|[_])+\S+$/,
            trigger: 'blur'
          }
        ],
        appName: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        appCategory: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        pcPageUrl: [
          {
            required: true,
            message: 'url',
            pattern: /^(http|ftp|https){1}[:]{1}[/]{2}([0-9a-zA-Z]|[-]|[_])+\S+$/,
            trigger: 'blur'
          }
        ],
        pcMenuIcon: [
          {
            required: true,
            message:
              'iview: https://www.iviewui.com/components/icon',
            pattern: /^\S+[-]{1}\S+$/,
            trigger: 'blur'
          }
        ],
        menuSort: [
          {
            required: true,
            message: 'PC4',
            pattern: /^[0-9]{1,4}$/,
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted: function() {
    this.loadData()
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    loadData() {
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.appInfoFindById,
        { data: this.appInfoFrom },
        response => {
          this.appInfoFrom = response.data.datas
        }
      )
    },
    commitData() {
      this.$store.commit('statusGlobalButtonLoding')
      this.appInfoFrom.id = this.$route.params.id
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.appInfoUpdate,
        this.appInfoFrom,
        () => {
          this.$Message.success('!')
          this.$store.commit('statusGlobalButtonLoding')
          this.$router.push('/app-info-list')
        },
        () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      )
    },
    showOnOpenApi() {
      return this.appInfoFrom.authType === '3' ||
        this.appInfoFrom.authType === '1' ||
        this.appInfoFrom.authType === '0'
        ? true
        : false
    },
    showOnPrivateTokenAuthType() {
      return this.appInfoFrom.authType === '1' ? true : false
    },
    showOnInnerApi() {
      return this.appInfoFrom.authType === '2' ? true : false
    },
    showOnBasicApp() {
      return this.appInfoFrom.appCategory === '0' ? true : false
    },
    notShowOnBasicApp() {
      return !this.showOnBasicApp()
    },
    showOnSso() {
      return this.appInfoFrom.loginType === '1' ||
        this.appInfoFrom.loginType === '2'
        ? true
        : false
    }
  }
}
</script>
