<style>
</style>


<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>
    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="appInfoFrom"
          :model="appInfoFrom"
          :rules="appInfoFromValidate"
          :label-width="100">
          <FormItem label=""
            prop="appName">
            <i-input v-model="appInfoFrom.appName"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="appCategory">
            <Select v-model="appInfoFrom.appCategory"
              placeholder="">
              <Option v-for="item in appCategoryEunmArr"
                :value="item.value"
                :disabled="item.disabled"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label="API"
            prop="authType"
            v-if="notShowOnBasicApp()">
            <Select v-model="appInfoFrom.authType"
              placeholder="API">
              <Option v-for="item in authTypeEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label=""
            prop="loginType"
            v-if="showOnPrivateTokenAuthType() && notShowOnBasicApp()">
            <Select v-model="appInfoFrom.loginType"
              placeholder="">
              <Option v-for="item in loginTypeEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label="SSO"
            prop="oauthBaseUrl"
            v-if="notShowOnBasicApp() && showOnSso()">
            <i-input v-model="appInfoFrom.oauthBaseUrl"
              placeholder="SSO"></i-input>
          </FormItem>

          <FormItem label="APP"
            prop="appStatus">
            <Select v-model="appInfoFrom.appStatus"
              placeholder="">
              <Option v-for="item in appStatusEunmArr"
                :value="item.value"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label="PC"
            prop="pcPageUrl"
            v-if="showOnBasicApp()">
            <i-input v-model="appInfoFrom.pcPageUrl"
              placeholder="PC"></i-input>
          </FormItem>

          <FormItem label="PCicon"
            prop="pcMenuIcon"
            v-if="showOnBasicApp()">
            <i-input v-model="appInfoFrom.pcMenuIcon"
              placeholder="PCicon"></i-input>
          </FormItem>

          <FormItem label="PC"
            prop="menuSort"
            v-if="showOnBasicApp()">
            <i-input v-model="appInfoFrom.menuSort"
              placeholder="PCicon"></i-input>
          </FormItem>

          <FormItem>
            <Button type="primary"
              :disabled="globalButtonLoding"
              @click="handleSubmit('appInfoFrom')"></Button>
            <Button @click="handleReset('appInfoFrom')"
              style="margin-left: 8px"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
  </div>

</template>


<script>
export default {
  data() {
    return {
      routerPath: this.$route.path,
      loginTypeEunmArr: [
        {
          value: '0',
          label: 'API (//)'
        },
        {
          value: '1',
          label: 'SSO ()'
        },
        {
          value: '2',
          label: 'SSO ()'
        }
      ],
      authTypeEunmArr: [
        {
          value: '0',
          label: ' ()'
        },
        {
          value: '1',
          label: '  ( )'
        },
        {
          value: '2',
          label: 'IP+ ()'
        },
        {
          value: '3',
          label: ' ( )'
        }
      ],
      appStatusEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      appCategoryEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      appInfoFrom: {
        appName: '',
        appCategory: '',
        menuSort: 0,
        authType: '',
        appStatus: '',
        pcPageUrl: '',
        pcMenuIcon: '',
        loginType: ''
      },
      appInfoFromValidate: {
        appName: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        appStatus: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        authType: [
          {
            required: true,
            message: 'API',
            trigger: 'blur'
          }
        ],
        loginType: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        appCategory: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        pcPageUrl: [
          {
            required: true,
            message: 'url',
            pattern: /^(http|ftp|https){1}[:]{1}[/]{2}([0-9a-zA-Z]|[-]|[_])+\S+$/,
            trigger: 'blur'
          }
        ],
        oauthBaseUrl: [
          {
            required: true,
            message: 'url',
            pattern: /^(http|ftp|https){1}[:]{1}[/]{2}([0-9a-zA-Z]|[-]|[_])+\S+$/,
            trigger: 'blur'
          }
        ],
        menuSort: [
          {
            required: true,
            message: 'PC4',
            pattern: /^[0-9]{1,4}$/,
            trigger: 'blur'
          }
        ],
        pcMenuIcon: [
          {
            required: true,
            message:
              'iview: https://www.iviewui.com/components/icon',
            pattern: /^\S+[-]{1}\S+$/,
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields()
    },
    commitData() {
      this.$store.commit('statusGlobalButtonLoding')
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.appInfoAdd,
        this.appInfoFrom,
        () => {
          this.$Message.success('!')
          this.$store.commit('statusGlobalButtonLoding')
          this.$router.push('/app-info-list')
        },
        () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      )
    },
    showOnBasicApp() {
      return this.appInfoFrom.appCategory === '0' ? true : false
    },
    notShowOnBasicApp() {
      return !this.showOnBasicApp()
    },
    showOnPrivateTokenAuthType() {
      return this.appInfoFrom.authType === '1' ? true : false
    },
    showOnSso() {
      return this.appInfoFrom.loginType === '1' ||
        this.appInfoFrom.loginType === '2'
        ? true
        : false
    }
  }
}
</script>
