<style>
</style>


<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>
    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="serverMachineNodeInfoFrom"
          :model="serverMachineNodeInfoFrom"
          :rules="serverMachineNodeInfoFromValidate"
          :label-width="150">

          <FormItem label="IP"
            prop="ipAddress">
            <i-input v-model="serverMachineNodeInfoFrom.ipAddress"
              placeholder="IP"></i-input>
          </FormItem>

          <FormItem label=""
            prop="accessPort">
            <i-input v-model="serverMachineNodeInfoFrom.accessPort"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="accessUser">
            <i-input v-model="serverMachineNodeInfoFrom.accessUser"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="accessAuthType">
            <Select v-model="serverMachineNodeInfoFrom.accessAuthType"
              placeholder="">
              <Option v-for="item in accessAuthTypeEunmArr"
                :value="item.value"
                :disabled="item.disabled"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label=""
            :rules="accessPasswordValidateRules()"
            v-show="showOnPassword()"
            prop="accessPassword">
            <i-input v-model="serverMachineNodeInfoFrom.accessPassword"
              type="password"
              password="true"
              :disabled="serverMachineNodeInfoFrom.accessAuthType != accessAuthTypeEunmArr[0].value"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            :rules="accessRsaPathValidateRules()"
            v-show="showOnRsa()"
            prop="accessRsaPath">
            <i-input v-model="serverMachineNodeInfoFrom.accessRsaPath"
              :disabled="serverMachineNodeInfoFrom.accessAuthType != accessAuthTypeEunmArr[1].value"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="serverMachineType">
            <Select v-model="serverMachineNodeInfoFrom.serverMachineType"
              placeholder="">
              <Option v-for="item in serverMachineTypeEunmArr"
                :value="item.value"
                :disabled="item.disabled"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem>
            <Button type="primary" :disabled="globalButtonLoding"
              @click="handleSubmit('serverMachineNodeInfoFrom')"></Button>
            <Button @click="handleReset('serverMachineNodeInfoFrom')"
              style="margin-left: 8px"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
    <Spin size="large"
      fix
      v-if="globalScreenLoding" />
  </div>

</template>


<script>
export default {
  data() {
    return {
      routerPath: this.$route.path,
      serverMachineTypeEunmArr: [
        {
          value: '0',
          label: 'openresty'
        },
        {
          value: '1',
          label: 'kubernetes-master'
        }
      ],
      accessAuthTypeEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      serverMachineNodeInfoFrom: {
        serverId: '',
        apiName: ''
      },
      serverMachineNodeInfoFromValidate: {
        serverMachineType: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        ipAddress: [
          {
            required: true,
            pattern: /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/,
            message: 'ip',
            trigger: 'blur'
          }
        ],
        accessPort: [
          {
            required: true,
            pattern: /^[0-9]{2,6}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        accessUser: [
          {
            required: true,
            max: 20,
            pattern: /^[a-zA-Z]+$/,
            message: ',,20',
            trigger: 'blur'
          }
        ],
        accessAuthType: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        accessPassword: [
          {
            required: true,
            max: 20,
            mix: 5,
            message: ',5,20',
            trigger: 'blur'
          }
        ],
        accessRsaPath: [
          {
            required: true,
            pattern: /^\/\S*[A-Za-z0-9]+$/,
            message: ',`/` ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    },
    globalScreenLoding: function() {
      return this.$store.state.globalScreenLoding
    },
    globalButtonLoding: function() {
      return this.$store.state.globalButtonLoding
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields()
    },
    commitData() {
      this.$store.commit('statusGlobalButtonLoding')
      this.utils.netUtil.post(
        this.$store,
        this.API_PTAH.serverMachineNodeInfoAdd,
        this.serverMachineNodeInfoFrom,
        () => {
          this.$Message.success('!')
          this.$store.commit('statusGlobalButtonLoding')
          this.$router.push('/server-machine-node-info-list')
        },
        () => {
          this.$store.commit('statusGlobalButtonLoding')
        }
      )
    },
    accessPasswordValidateRules() {
      let tempRuls = this.serverMachineNodeInfoFromValidate.accessPassword
      let required =
        this.serverMachineNodeInfoFrom.accessAuthType !=
        this.accessAuthTypeEunmArr[1].value
      tempRuls[0].required = required
      return tempRuls
    },
    accessRsaPathValidateRules() {
      let tempRuls = this.serverMachineNodeInfoFromValidate.accessRsaPath
      let required =
        this.serverMachineNodeInfoFrom.accessAuthType !=
        this.accessAuthTypeEunmArr[0].value
      tempRuls[0].required = required
      return tempRuls
    },
    showOnPassword() {
      return (
        this.serverMachineNodeInfoFrom.accessAuthType ==
        this.accessAuthTypeEunmArr[0].value
      )
    },
    showOnRsa() {
      return (
        this.serverMachineNodeInfoFrom.accessAuthType ==
        this.accessAuthTypeEunmArr[1].value
      )
    }
  }
}
</script>
